import wx from 'weixin-js-sdk'
import store from '../store/index'
import apiHelper from '../apis/apiHelper'

export default {
  initConfig (url) {
    if (!/micromessenger/i.test(navigator.userAgent)) {
      return
    }
    // iOS App URL  wxConfig iOS 
    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
      //iOS 
      if (!store.state.firstLink) {
        store.commit('SET_FIRSTLINK', url)
      } else {
        url = store.state.firstLink
      }
    }
    //  url 
    apiHelper.purePost('http://cnnapi.ngrok.tecfcs.com/common/jsapi', {signurl: url}).then(res => {
      // configreadyconfigconfig
      if (res) {
        wx.config({
          debug: false, // ,apialertpclogpc
          appId: res.appId,   // 
          nonceStr: res.nonceStr,   // 
          signature: res.signature, // 1
          timestamp: res.timestamp, // 
          jsApiList: [
            'checkJsApi',
            "updateAppMessageShareData",
            "updateTimelineShareData",
            "onMenuShareAppMessage",
            "onMenuShareTimeline",
            'scanQRCode',
            'playVoice',
            'pauseVoice',
            'onVoicePlayEnd',
            'stopVoice',
            'uploadVoice',
            'chooseImage',
            'previewImage',
            'downloadImage',
            'downloadVoice',
            'uploadImage',
            'chooseWXPay'
          ], // JSJS2
        })
        wx.ready(function(){
          console.log('conf ok')
          wx.checkJsApi({
            jsApiList: [
              'onMenuShareTimeline',
              'onMenuShareAppMessage',
              'onMenuShareQQ',
              'onMenuShareWeibo',
              'onMenuShareQZone',
              'scanQRCode',
              'playVoice',
              'pauseVoice',
              'onVoicePlayEnd',
              'stopVoice',
              'uploadVoice',
              'chooseImage',
              'previewImage',
              'downloadImage',
              'downloadVoice',
              'uploadImage',
              'chooseWXPay'
            ],
            success: function (res) {
              //alert(JSON.stringify(res));
              //alert(JSON.stringify(res.checkResult.getLocation));
              if (res.checkResult.getLocation == false) {
                alert('JS');
                return;
              }
            }
          });
          // configreadyconfigconfigreadyready
        });
        wx.error((res) => {
          console.log(res);
          // wx.closeWindow();
          // configerrorconfigdebugresSPA
        });
      }
    })
  },
  // 
  share (url, title, desc, imgUrl, pageType) {
    if (!/micromessenger/i.test(navigator.userAgent)) {
      return
    }
    //
    let shareUrl = location.origin + '/'
    if (url) {
      shareUrl = url
    }
    let shareTitle = ''
    if (title) {
      shareTitle = title
    }
    let shareDesc = ''
    if (desc) {
      shareDesc = desc
    }
    let shareImg = ''
    if (imgUrl) {
      shareImg = imgUrl
    }

    /*
    * ,:
       from=timeline&isappinstalled=0
       from=groupmessage&isappinstalled=0
       from=singlemessage&isappinstalled=0
    *
    * */

    wx.ready(() => {
      // ready
      //  weixin-js-sdk 1.4  updateAppMessageShareDataupdateTimelineShareData
      // 

      // 
      wx.updateAppMessageShareData({
        title: shareTitle, // 
        desc: shareDesc, // 
        link: shareUrl, //  
        imgUrl: shareImg, // 
        success: ()=>{
          console.log('share success!')
          //  
          // successCallback(openid)
          //
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/
      })
      // 
      wx.updateTimelineShareData({
        title: shareTitle,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 
          // 
          if(pageType === 'payPage'){

          }
          // successCallback()
          //
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/
      })
      wx.onMenuShareAppMessage({ //  updateAppMessageShareData
        title: shareTitle,
        desc: shareDesc,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 
          // successCallback()
          //
        },
       /* fail: (err) => {
          errorCallback(err)
        }*/

      })
      wx.onMenuShareTimeline({ // updateTimelineShareData
        title: shareTitle,
        link: shareUrl,
        imgUrl: shareImg,
        success: ()=>{
          console.log('share success!')
          // 
          // successCallback()
          //
        },

      });
      // 
      wx.hideMenuItems({ // 'menuItem:copyUrl',
        menuList: ['menuItem:favorite', 'menuItem:editTag', 'menuItem:delete', 'menuItem:originPage', 'menuItem:readMode', 'menuItem:openWithQQBrowser', 'menuItem:openWithSafari', 'menuItem:share:email', 'menuItem:share:brand', 'menuItem:share:qq', 'menuItem:share:QZone', 'menuItem:share:facebook', 'menuItem:share:weiboApp']
      });
      wx.error(res => {
        // configerror
        // configdebugresSPA
        console.log(res);
        // configErrorCallback(res)
      })
    })
  },
}



