import axios from 'axios/index'
import {Toast} from 'vant'
import router from '../router'
import store from '../store'
import { getToken } from '../utils/auth'
//vue 
let vue = undefined;
//toastloading
let showToast = false;
let showLoading = false;

let BASE_URL = 'http://cnnapi.ngrok.tecfcs.com';
//
if (window.location.origin.indexOf('http://cnnapi.ngrok.tecfcs.com') !== -1) {
  BASE_URL = 'http://cnnapi.ngrok.tecfcs.com'
}
// axios.defaults.withCredentials = true;
axios.defaults.timeout = 20000;

//
axios.interceptors.request.use(
 config => {
     // Do something before request is sent
     if (store.state.user.token) {
         // token-- ['wx-token']key 
         config.headers['wx-token'] = getToken()
     }
  if (showLoading) {
    Toast.loading({
      mask: true,
      duration: 0,
    })
  }
  return config
}, error => {
  if (vue && showToast) {
    vue.$toast('!')
  }
  if (showLoading) {
    Toast.clear()
  }
  return Promise.reject(error)
});

//
axios.interceptors.response.use(response => {
  if (showLoading) {
    Toast.clear()
  }
  const status = response.data.code || 200;
  //401
  if (status === 401){
    window.localStorage.clear();
    router.replace('/wxAuth')
  }
  //
  if (response.data) {
    return response.data
  }
}, error => {
  if (showLoading) {
    Toast.clear()
  }
  if (error.response) {
    //  2xx 
    console.error('response-error-data', error.response.data);
    if (error.response.data.msg != null && error.response.data.msg.length > 0) {
      if (vue && showToast) {
        vue.$toast(error.response.data.msg)
      }
    }
  } else {
    //
    console.error('Error', error.message);
    if (vue && showToast) {
      vue.$toast('')
    }
  }
  
  return Promise.reject(error)
});

export default class apiHelper {
  static pureGet = (url, params) => {
    vue = undefined;
    showToast = false;
    showLoading = false;
    
    return axios({
      method: 'get',
      url: url,
      params: params,
      baseURL: BASE_URL,
    })
  };
  
  static purePost = (url, params) => {
    // vue = undefined
    showToast = false;
    showLoading = false;
    
    return axios({
      method: 'post',
      url: url,
      data: params,
      baseURL: BASE_URL,
    })
  };
  
  static get = (vueContext, url, isShow) => {
    showToast = true;
    showLoading = true;
    if (vueContext != null) {
      vue = vueContext
    }
    
    if (isShow === false) {
      showToast = isShow
    }
    
    return axios({
      method: 'get',
      url: url,
      params: params,
      baseURL: BASE_URL,
    })
  };
  
  static post = (vueContext, url, params, isShow) => {
    showToast = true;
    showLoading = true;
    if (vueContext != null) {
      vue = vueContext
    }
    
    if (isShow === false) {
      showToast = isShow
    }
    
    return axios({
      method: 'post',
      url: url,
      data: params,
      baseURL: BASE_URL,
    })
  }
};
